/* stylelint-disable */

@mixin big-desktop {
  @media (min-width: (1440px)) {
    @content;
  }
}

@mixin desktop {
  @media (min-width: (1024px)) {
    @content;
  }
}

@mixin tablet {
  @media (min-width: (768px)) {
    @content;
  }
}

@mixin big-mobile {
  @media (min-width: (576px)) {
    @content;
  }
}

@mixin mobile {
  @media (min-width: (376px)) {
    @content;
  }
}
